@import url('https://fonts.googleapis.com/css2?family=Rampart+One&display=swap');
* {
  font-family: 'Rampart One', cursive;
  padding: 0;
  margin: 0;
}
body {
  padding: 1rem 2rem;
  background: #000;
  color: whitesmoke;
  text-align: center;
}
h2{
  font-size: 2.5rem;
}
input{
  background: rgba(255,255,255,.1);
  border: none;
  border-radius: .5rem;
  color: whitesmoke;
  margin: 1rem 0;
  font-size: 1.2rem;
  padding: .3rem;
  text-align: center;
  width: 50%;
}
input::after{
  border: none;
}
h3{
  margin: .5rem;
}
.card-grid {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: 13rem;
  grid-gap: 10px;
}
.card{
  border: 3px solid rgba(255, 255, 255, .8);
  border-radius: .3rem;
  overflow: hidden;
}

.card:nth-child(3),
.card:nth-child(4),
.card:nth-child(9) {
  grid-column-start: span 2;
}
.card:nth-child(5) {
  grid-column-start: span 2;
  grid-row-start: span 2;
}
.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
@media screen and (max-width: 900px) {
  .card-grid {
    grid-auto-rows: 7rem;
  }
}
@media screen and (max-width: 450px) {
  .card-grid {
    grid-auto-rows: 4rem;
    grid-gap: 5px;
  }
}